@import url('https://fonts.googleapis.com/css2?family=Holtwood+One+SC&display=swap');

.details-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 70px;
}

.overlay {
  width: 85%;
  height: 80%;
  display: flex;
  justify-content: center;
  background: rgb(220 214 214 / 20%);
  box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
  border-radius: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.125);
}

.img-poster {
  height: 450px;
  margin: 10px 10px 10px 15px;
  border-radius: 25px;
}

.details-right {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 90px;
}

.div-detalis-film {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}

.title-evaluation {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
}

.star-container {
  position: relative;
  margin-top: 20px;
  margin-left: 50px;
}

.svg-star {
  width: 60px;
  height: auto;
}

.evaluation {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(60%, 5%);
  color: white;
  font-size: 20px;
  font-weight: 500;
}

.title-overview-genres {
  margin: 0px 0px 5px 0px;
}

.additional-info {
  margin-top: 20px;
}

.h2-additional-info {
  font-size: 30px;
  margin-bottom: 10px;
  font-family: 'Holtwood One SC', sans-serif;
  background: linear-gradient(
    to right,
    blue,
    pink,
    rgb(60, 13, 228),
    #015eff,
    #051857,
    #21ffff,
    rgb(44, 90, 255),
    rgb(235, 72, 224),
    rgb(173, 41, 255)
  );
  background-size: 200% 200%;
  animation: gradient-animation 5s ease infinite;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 1px black;
}

.button-cast {
  background-color: rgb(220 214 214 / 20%);
  border-radius: 15px;
  border-color: transparent;
  width: 150px;
  height: 100px;
  color: rgba(0, 238, 255, 0.866);
  -webkit-text-stroke: 0.5px black;
  font-size: 30px;
  font-weight: 700;
  margin-right: 40px;
}

.button-reviews {
  background-color: rgb(220 214 214 / 20%);
  border-radius: 15px;
  border-color: transparent;
  width: 150px;
  height: 100px;
  color: rgba(0, 238, 255, 0.866);
  -webkit-text-stroke: 0.5px black;
  font-size: 30px;
  font-weight: 700;
}

.ul-actors {
  display: flex;
  margin-top: 55px;
  list-style-type: none;
  gap: 45px;
  flex-flow: wrap;
}

.ul-actors-p {
  width: 200px;
  text-align: left;
}

.ul-all-reviews {
  margin-right: 90px;
}

.item-review {
  text-align: start;
}

/* modal */

.modalOverly {
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  background: rgba(18, 20, 23, 0.5);
}

.modalContent {
  background-color: #182ca3de;
  width: 95%;
  height: 95%;
  margin: 20px auto;
  text-align: center;
  position: relative;
  border-radius: 10px;
  box-shadow: 0px 4px 14px 0px black;
  overflow-y: auto;
}

.closeModalBtn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  background: none;
  border: none;
  cursor: pointer;
  color: white;
}

@media screen and (max-width: 768px) {
  .details-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 30px;
  }

  .overlay {
    width: 90%;
    height: 80%;
    display: block;
    justify-content: center;
    background: rgb(220 214 214 / 20%);
    box-shadow: 0 0.75rem 2rem 0 rgba(0, 0, 0, 0.1);
    border-radius: 2rem;
    border: 1px solid rgba(255, 255, 255, 0.125);
  }

  .img-poster {
    height: 400px;
    margin: 10px 10px 10px 10px;
    border-radius: 25px;
  }

  .title-evaluation {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 110%;
    font-size: 11px;
  }

  .star-container {
    position: relative;
    margin-top: 0px;
    margin-left: 115px;
  }

  .title-overview-genres {
    margin: 0px 0px 5px 0px;
    font-size: 20px;
  }

  .h2-additional-info {
    font-size: 28px;
    margin-bottom: 10px;
    font-family: 'Holtwood One SC', sans-serif;
    background: linear-gradient(
      to right,
      blue,
      pink,
      rgb(60, 13, 228),
      #015eff,
      #051857,
      #21ffff,
      rgb(44, 90, 255),
      rgb(235, 72, 224),
      rgb(173, 41, 255)
    );
    background-size: 200% 200%;
    animation: gradient-animation 5s ease infinite;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 1px black;
    text-align: center;
  }

  .button-cast {
    background-color: rgb(220 214 214 / 20%);
    border-radius: 15px;
    border-color: transparent;
    width: 130px;
    height: 80px;
    color: rgba(0, 238, 255, 0.866);
    -webkit-text-stroke: 0.5px black;
    font-size: 25px;
    font-weight: 700;
    margin-right: 10px;
  }

  .button-reviews {
    background-color: rgb(220 214 214 / 20%);
    border-radius: 15px;
    border-color: transparent;
    width: 130px;
    height: 80px;
    color: rgba(0, 238, 255, 0.866);
    -webkit-text-stroke: 0.5px black;
    font-size: 25px;
    font-weight: 700;
  }

  .ul-actors {
    display: flex;
    margin-top: 55px;
    list-style-type: none;
    gap: 45px;
    flex-flow: wrap;
    padding: 0px;
    justify-content: center;
  }
}
